#content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 100vh;
  background: linear-gradient(to bottom, #eaf2f0, #418583);
}
#quote-box {
  padding: 0;
}

blockquote {
  margin: 3.7em auto;
  padding: 2em;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background: linear-gradient(white, white) padding-box,
    url(https://picsum.photos/id/1019/200/300/?blur=2) border-box 0 / cover;
  border: 2em solid transparent;
  box-shadow: 5px 3px 30px black;
  font-size: 1.4em;
  font-family: "Lora", serif;
  font-style: italic;
  line-height: 1.5;
  max-width: 80%;
}
footer a {
  list-style: none;
  color: #000;
}
#link {
  color: #000;
}
#btn-container {
  padding: 20px;
}
#tweet-quote {
  padding: 2px;
  margin: 0px;
}
#btn-new-qoute {
  padding: 2px;
  margin: 0px;
}

footer {
  padding-top: 1.3em;
}

cite {
  font-style: normal;
  font-size: 1em;
  font-weight: bold;
}

@import url(https://fonts.googleapis.com/css?family=Lora);

@media only screen and (max-width: 800px) {
  blockquote {
    margin: 3.7em auto;
    padding: 2em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background: linear-gradient(white, white) padding-box,
      url(https://picsum.photos/id/1019/200/300/?blur=2) border-box 0 / cover;
    border: 2em solid transparent;
    box-shadow: 5px 3px 30px black;
    font-size: 1.4em;
    font-family: "Lora", serif;
    font-style: italic;
    line-height: 1.5;
    max-width: 80%;
    overflow: hidden;
  }
  footer a {
    list-style: none;
    color: #000;
  }
  #link {
    color: #000;
  }
  #btn-container {
    padding: 20px;
  }
  #tweet-quote {
    padding: 2px;
    margin: 0px;
  }
  #btn-new-qoute {
    padding: 2px;
    margin: 0px;
  }

  footer {
    display: flex;
    position: absolute;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  cite {
    font-style: normal;
    font-size: 1em;
    font-weight: bold;
  }

  #content {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
  }
  body {
    background: linear-gradient(to bottom, #eaf2f0, #418583);
  }
  .box {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    box-sizing: border-box;
    width: 93%;
    /* object-fit: contain; */

    align-items: center;
  }
  #heading {
    padding: 1.3rem;
    margin-top: 0;
    font-size: 2.5rem;
  }
  @media screen and (min-width: 800px) {
    .box {
      margin-left: auto;
      margin-right: auto;
    }
    #content {
      right: 10%;
    }
  }
}
.wrapper {
  scroll-behavior: smooth;
}
